import * as React from "react";
import { Box } from "@mui/system";
import { Grid } from "@mui/material";
import { useSelector } from "react-redux";
import MarketplaceCard from "../../MarketplaceCard/MarketplaceCard";
import { EmptyData } from "../../AdminDashbord/EmptyData";
import { useState } from "react";
import { useEffect } from "react";
export default function AllProducts({ Nfts, favourite, faildNFT }) {
  const [Nftsrecord, setRecord] = useState([]);
  const { ceilSlice } = useSelector((state) => state);
  const { userNfts, ceildetails, failsNfts } = ceilSlice;
  const [nftmint, setNftmint] = useState(false);
  let lenOFceil = 0;
  const checker = true;
  useEffect(() => {
    (async function () {
      if (Nfts === true || Nfts === false) {
        if (Nfts) {
          setRecord(ceildetails);
        }
      }
      if (favourite === false) {
        setRecord(userNfts);
      }
      if (faildNFT === true) {
        setRecord(failsNfts);
      }
    })();
  }, [ceilSlice, nftmint]);
  return (
    <Box sx={{ width: "100%", mx: "auto" }}>
      <Grid container my={2}>
        {Nftsrecord?.length > lenOFceil ? (
          Nftsrecord?.map(
            (
              {
                address,
                _id,
                name,
                lang,
                long,
                filelink,
                capturesImage,
                file,
                productTotalPrice,
                user,
                favoritesList,
                likesList,
                howManyViews,
                paidStatusContract,
              },
              index
            ) => (
              <Grid item xs={12} sm={6} md={4} lg={3} p={2} key={index + _id}>
                <MarketplaceCard
                  address={address}
                  locations={{ lang, long }}
                  id={_id}
                  name={name}
                  Image={capturesImage}
                  user={user}
                  filelink={filelink}
                  file={file}
                  productTotalPrice={productTotalPrice}
                  checker={checker}
                  userlikeviews={{ likesList, favoritesList }}
                  howManyViews={howManyViews}
                  paidStatusContract={paidStatusContract}
                  failedNftmint={setNftmint}
                />
              </Grid>
            )
          )
        ) : (
          <EmptyData text=" Empty  NFTs ...." />
        )}
      </Grid>
    </Box>
  );
}
